import Layout from '../components/layout';

// game components
import Desk from '../components/desk';
import Square from '../components/square';
import Button from '../components/button';

const bombAmounts = [ 4, 8, 16, 32]; // used to generate bomb counts

function randomIntFromInterval(min, max) // min and max included
{
	return Math.floor(Math.random() * (max - min + 1) + min);
}

class Index extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			boardSize: 9,
			numberOfBombs: 8,
			bombs: [],
			numbers: {},
			clearSpots: [],
			uncoveredTiles: new Set(),
			gameState: "playing",
			gameID: 0
		};
	}

	componentDidMount() {
		this.startGame();
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevState.numberOfBombs != this.state.numberOfBombs) // if the number of bombs is different then restart the game
			this.startGame();
		else if (this.state.gameState == "playing") // if the state is playing check if we won 
			this.checkIfWon();
	}

	checkIfWon() {
		if (this.state.uncoveredTiles.size >= (this.state.boardSize * this.state.boardSize) - this.state.numberOfBombs && this.state.uncoveredTiles.size > 0) {
			this.setState({
				gameState: "won"
			});
		}
	}

	startGame() { // resets game
		let bombs = [];
		let numbers = {};
		let exclude = new Set();

		for (let i = 0; i < this.state.numberOfBombs; i++) {

			let tmpX = randomIntFromInterval(0, this.state.boardSize - 1)
			let tmpY = randomIntFromInterval(0, this.state.boardSize - 1)

			while (exclude.has(tmpX.toString() + tmpY.toString())) { // regenerate coords if bomb space already taken
				tmpX = randomIntFromInterval(0, this.state.boardSize - 1)
				tmpY = randomIntFromInterval(0, this.state.boardSize - 1)
			}

			let bomb = {
				x: tmpX,
				y: tmpY
			};

			exclude.add(bomb.x.toString() + bomb.y.toString()); // used to check for valid bomb placement
			bombs.push(bomb);

			for (let iy = -1; iy <= 1; iy++) {
				for (let ix = -1; ix <= 1; ix++) {
					let x = bomb.x + ix;
					let y = bomb.y + iy;
					let str = x.toString() + y.toString();

					if (x >= 0 && y >= 0 && x <= this.state.boardSize - 1 && y <= this.state.boardSize - 1) {
						if (numbers.hasOwnProperty(str))
							numbers[str] = numbers[str] + 1;
						else
							numbers[str] = 1;
					}
				}
			}
		}

		bombs.forEach(bomb => { // clear any numbers placed on bombs
			let str = bomb.x.toString() + bomb.y.toString();
			numbers[str] = undefined;
		});

		this.setState({
			bombs,
			numbers,
			gameState: "playing",
			gameID: this.state.gameID + 1,
			uncoveredTiles: new Set()
		});
	}

	clearBoard(_x, _y) { // this is used to clear connected blank squares on the board
		let spotsToCheck = [{
			x: _x,
			y: _y,
		}];

		let spotsChecked = []

		let clearSpots = []

		while (spotsToCheck.length > 0) {
			let spot = JSON.parse(JSON.stringify(spotsToCheck[0]));
			spotsToCheck.splice(0, 1);
			spotsChecked.push(spot.x.toString() + spot.y.toString());

			for (let iy = -1; iy <= 1; iy++) { // add all the spots that are connected
				for (let ix = -1; ix <= 1; ix++) {
					let x = spot.x + ix;
					let y = spot.y + iy;
					let str = x.toString() + y.toString();

					if (x >= 0 && y >= 0 && x <= this.state.boardSize - 1 && y <= this.state.boardSize - 1) {
						if (!this.state.numbers.hasOwnProperty(str) && !clearSpots.includes(str) && !spotsChecked.includes(str))
							spotsToCheck.push({
								x,
								y
							});
						if (-1 == this.state.bombs.findIndex(bomb => bomb.x == x && bomb.y == y))
							clearSpots.push(str);
					}
				}
			}
		}

		clearSpots.forEach(element => {
			this.state.uncoveredTiles.add(element);
		});

		this.setState({
			clearSpots
		});
	}

	render() {
		return (
			<>
				<div style={{
					top: "-32px",
					right: "0",
					bottom: "0",
					left: "0",
					position: "fixed",
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					flexDirection: "column"
				}}>
					<Layout title={`Lily's Minesweeper`}>
						<Desk boardSize={this.state.boardSize}>
							{[...Array(this.state.boardSize).keys()].map(x => ( // render the grid
								[...Array(this.state.boardSize).keys()].map(y => (
									<Square
										key={x.toString() + y.toString()}
										parent={this.state}
										onClick={() => {
											if (-1 != this.state.bombs.findIndex(bomb => bomb.x == x && bomb.y == y)) // check if bomb on click
												this.setState({
													gameState: "dead"
												})
											else {
												if (!this.state.numbers.hasOwnProperty(x.toString() + y.toString())) // check if empty square
													this.clearBoard(x, y);
												else
													this.setState({ // if not empty or a bomb just uncover that one space
														uncoveredTiles: this.state.uncoveredTiles.add(x.toString() + y.toString())
													})
											}
										}}
										keyStr={x.toString() + y.toString()}
										isBomb={-1 != this.state.bombs.findIndex(bomb => bomb.x == x && bomb.y == y)}
									>
										{this.state.numbers.hasOwnProperty(x.toString() + y.toString()) ? this.state.numbers[x.toString() + y.toString()] : ""}
									</Square>
								))
							))}
						</Desk>
						<div style={{
							margin: "24px 0 0 0",
							display: "flex",
							justifyContent: "center",
							flexDirection: "column",
							width: "200px"
						}}>
							{this.state.gameState == "playing" || this.state.gameState == "dead" || this.state.gameState == "won" ?
								<>
									<div style={{
										display: "flex",
										justifyContent: "space-between",
										marginBottom: "8px"
									}}>
										{bombAmounts.map(bombCount => {
											return <Button
												color={this.state.numberOfBombs == bombCount ? "#F44336" : "#666"}
												onClick={() => {
													this.setState({ numberOfBombs: bombCount })
												}}>{bombCount}</Button>
										})}
									</div>
									<Button
										color="#FFCA28"
										onClick={() => {
											this.startGame();
										}}>Restart</Button>
								</>
								:
								this.state.gameState == "viewboard" ?
									<>
										<div style={{
											display: "flex",
											justifyContent: "space-between",
											marginBottom: "8px"
										}}>
											{bombAmounts.map(bombCount => {
												return <Button
													color={this.state.numberOfBombs == bombCount ? "#F44336" : "#666"}
													onClick={() => {
														this.setState({ numberOfBombs: bombCount })
													}}>{bombCount}</Button>
											})}
										</div>
										<Button
											color="#03A9F4"
											onClick={() => {
												this.startGame();
											}}>Play Again?</Button>
									</>
									: null}
						</div>
					</Layout>
				</div>


				{this.state.gameState == "dead" ? // show death message
					<div style={{

						top: "0",
						right: "0",
						bottom: "0",
						left: "0",
						position: "fixed",
						display: "flex",
						justifyContent: "center",
						alignItems: "center",
						background: "rgba(0,0,0,0.5)",
						flexDirection: "column"
					}}>
						<h1 style={{
							color: "white"
						}}>Too bad! You exploded...</h1>

						<img style={{
							borderRadius: "8px",
							margin: "0 0 32px 0"
						}}
							src="https://media1.tenor.com/images/3197441d730ffde04b00fe169431ee89/tenor.gif?itemid=8587771"></img>

						<div>
							<Button
								color="#9E9E9E"
								style={{
									margin: "0 16px 0 0"
								}}
								onClick={() => {
									this.setState({
										gameState: "viewboard"
									})

									let clearSpots = [];

									this.state.bombs.forEach(bomb => {
										clearSpots.push(bomb.x.toString() + bomb.y.toString());
									});

									this.setState({
										clearSpots
									});
								}}>
								View Board
							</Button>
							<Button
								color="#03A9F4"
								onClick={() => {
									this.startGame();
								}}>
								Play Again?
							</Button>
						</div>
					</div>
					: null}

				{this.state.gameState == "won" ? // show won message
					<div style={{
						top: "0",
						right: "0",
						bottom: "0",
						left: "0",
						position: "fixed",
						display: "flex",
						justifyContent: "center",
						alignItems: "center",
						background: "rgba(0,0,0,0.5)",
						flexDirection: "column"
					}}>
						<h1 style={{
							color: "white"
						}}>Congrats you won!</h1>

						<img style={{
							borderRadius: "8px",
							margin: "0 0 32px 0"
						}}
							src="https://media1.tenor.com/images/4e4bef61e89a25568d9e3406beed3d35/tenor.gif?itemid=13721423"></img>

						<div>
							<Button
								color="#9E9E9E"
								style={{
									margin: "0 16px 0 0"
								}}
								onClick={() => {
									this.setState({
										gameState: "viewboard"
									})

									let clearSpots = [];

									this.state.bombs.forEach(bomb => {
										clearSpots.push(bomb.x.toString() + bomb.y.toString());
									});

									this.setState({
										clearSpots
									});
								}}>
								View Board
							</Button>
							<Button
								color="#03A9F4"
								onClick={() => {
									this.startGame();
								}}>
								Play Again?
							</Button>
						</div>
					</div>
					: null}
			</>
		);
	}
}

export default Index;