var ReactDOM = require('react-dom');

import Flag from './flag';
import Mine from './mine';
import Question from './question';

const baseSquareStyles = {
	width: 50,
	height: 50,
	padding: 10,
	margin: 2,
	cursor: 'pointer',
	backgroundColor: '#FFF',
	border: '1px solid grey',
	lineHeight: 1,
	textAlign: 'center',
	fontSize: 18,
	borderRadius: '4px',
	transition: "0.2s ease all"
};

class Square extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			state: "covered" // default state is covered so player can't see content
		};
	}

	componentDidMount() {
		ReactDOM.findDOMNode(this).addEventListener('contextmenu', (ev) => { // used to detect right click
			ev.preventDefault();
			switch (this.state.state) { // makes sure to cycle through all options for right click
				case "covered":
					this.setState({ state: "flag" })
					break;
				case "flag":
					this.setState({ state: "question" })
					break;
				case "question":
					this.setState({ state: "covered" })
					break;
			}
			return false;
		}, false);
	}

	componentWillUnmount() {
		ReactDOM.findDOMNode(this).removeEventListener('contextmenu'); // used to clean up right click detection
	}

	componentDidUpdate(prevProps) {
		if (prevProps.parent.gameID != this.props.parent.gameID) // resets all tiles on game load
			this.setState({
				state: "covered"
			})
		if (prevProps.parent.clearSpots != this.props.parent.clearSpots && 
			this.props.parent.clearSpots.includes(this.props.keyStr)) { // uncovers tiles that the main game has checked
			switch (this.state.state) {
				case "covered":
					this.setState({ state: "uncovered" })
					break;
				case "question": // decided all question tiles would also get uncovered
					this.setState({ state: "uncovered" })
					break;
			}
		}
	}

	getContent() {
		switch (this.state.state) { // gets tile content depending on state
			case "uncovered": return (this.props.isBomb) ? <Mine></Mine> : this.props.children
			case "covered": return;
			case "flag": return <Flag></Flag>
			case "question": return <Question></Question>
		}
	}

	getStyles() {
		let customStyles = {}; // gets extra styles for tile depending on state
		switch (this.state.state) {
			case "uncovered":
				if (this.props.isBomb)
					customStyles = {
						backgroundColor: "#F44336",
						color: "#f2f2f2",
						fill: "#f2f2f2"
					}
				else
					customStyles = {
						color: "#333",
						fill: "#333"
					}
				break;
			case "covered":
				customStyles = {
					backgroundColor: this.state.hovered ? "#0288D1" : "#03A9F4",
					color: "#f2f2f2",
					fill: "#f2f2f2"
				}
				break;
			case "flag":
				customStyles = {
					backgroundColor: this.state.hovered ? "#FFB300" : "#FFCA28",
					color: "#f2f2f2",
					fill: "#f2f2f2"
				}
				break;
			case "question":
				customStyles = {
					backgroundColor: this.state.hovered ? "#388E3C" : "#4CAF50",
					color: "#f2f2f2",
					fill: "#f2f2f2"
				}
				break;
		}

		return { ...baseSquareStyles, ...customStyles }
	}

	render() {
		return (
			<button
				style={this.getStyles()}
				onClick={(event) => {
					if(this.props.parent.gameState != "viewboard")
					{
						this.setState({ state: "uncovered" })
						this.props.onClick(event);
					}
				}}
				onMouseEnter={() => {
					if(this.props.parent.gameState != "viewboard")
						this.setState({
							hovered: true
						})
				}}
				onMouseLeave={() => {
					if(this.props.parent.gameState != "viewboard")
						this.setState({
							hovered: false
						})
				}}
			>{this.getContent()}</button>
		);
	}
}

export default Square;