import { StyleProvider } from '@cloudflare/style-nextjs';

class Layout extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			state: "covered"
		};
	}

	render() {
		return (
			<StyleProvider>
				<>
					{this.props.children}
				</>
			</StyleProvider>
		)
	}
}

export default Layout;