class Button extends React.Component {
	constructor(props) {
		super(props);
		this.state = {

		};
	}

	render() {
		return (
			<button
				style={{
					color: "#f2f2f2",
					background: this.state.hovered ? "#333" : this.props.color,
					border: "none",
					borderRadius: "8px",
					padding: "10px 14px",
					cursor: "pointer",
					transition: "0.2s ease all",
					textTransform: "uppercase",
					...this.props.style
				}}
				className="button"
				onClick={(event) => {
					this.props.onClick(event);
				}}
				onMouseEnter={(event) => {
					this.setState({
						hovered: true
					})
				}}
				onMouseLeave={(event) => {
					this.setState({
						hovered: false
					})
				}}
			>
				{this.props.children}
			</button>
		);
	}
}

export default Button;